.search-form {
  width: 100%;
  background: linear-gradient(315deg, rgba(80, 8, 235, .7) 3%, rgba(93, 164, 235, .7) 38%, rgba(163, 238, 233, .7) 68%, rgba(101, 78, 93, .7) 98%);
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 3px 5px rgba(0, 0, 0, .5);
}

.search-form-input {
  width: 300px;
  height: 30px;
  border: none;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  outline: none;
  padding: 5px 5px 5px 15px;
  font-size: 16px;
}

.search-form-button {
  width: 140px;
  height: 40px;
  border: none;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-weight: 600;
  display: flex;
}

